import { Component, OnInit, ElementRef, NgZone, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { CommonService } from "../services/commonService";
import { TimesheetService } from "../services/timesheetService";
import { MatDatepickerModule, MatNativeDateModule, DateAdapter } from '@angular/material';
import { MapsAPILoader } from '@agm/core';
import { } from 'googlemaps';
import { PasswordValidation } from '../forms/validationforms/password-validator.component';
import swal from 'sweetalert2';
import { DatePipe } from '@angular/common'


declare const $: any;
@Component({
    selector: 'app-view-timesheet',
    templateUrl: './view-timesheet.component.html',
    styleUrls: ['./view-timesheet.component.scss'],
    providers: [CommonService, TimesheetService, DatePipe]
})
export class ViewTimesheetComponent implements OnInit {
    public latitude: number;
    public longitude: number;
    public zoom: number;
    public timesheetTrack = {};
    public timesheetLatLong;
    public userProfile: any;
    public workOrdAsgtID: any
    public showhidecontrol = false;
    public selectedenddate: any;
    public selectedstartdate: any;
    public breakhour: any;
    public breakminute: any;
    public tsheetDailyId: string;
    public hidebuttons = true;
    public times = { year: 31557600, month: 2629746, day: 86400, hour: 3600, minute: 60, second: 1 }
    public worktime: string;
    public oldbreaktime: string;

    public viewTimesheetPage = new FormGroup({
        starttime: new FormControl(),
        endtime: new FormControl(),
        breakhour: new FormControl(),
        breakminute: new FormControl()
    });

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private timesheetService: TimesheetService,
        private activatedRoute: ActivatedRoute,
        private formBuilder: FormBuilder,
        public datepipe: DatePipe,
        private dateAdapter: DateAdapter<Date>,
    ) {
        dateAdapter.setLocale('en-GB');
    }

    isFieldValid(form: FormGroup, field: string) {
        return !form.get(field).valid && form.get(field).touched;
    }

    displayFieldCss(form: FormGroup, field: string) {
        return {
            'has-error': this.isFieldValid(form, field),
            'has-feedback': this.isFieldValid(form, field)
        };
    }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {

            this.activatedRoute.queryParams.subscribe((params: Params) => {
                if (params['woasgtID'] == undefined) {
                    this.router.navigate(['timesheet']);
                }
                else {
                    //var woAsgtID = params['woasgtID'];
                    this.workOrdAsgtID = params['woasgtID'];
                    this.tsheetDailyId = params['tsheetDailyID'];
                    //this.getWorkOrderDetails(this.workOrdAsgtID);
                    //this.getTimesheetDaily(this.workOrdAsgtID);
                    this.getWorkOrderDetails(this.tsheetDailyId);
                    this.getTimesheetDaily(this.tsheetDailyId);

                    let loggedinUserRoleId: Number = JSON.parse(sessionStorage.getItem('LogEmployee')).RoleId;

                    if (loggedinUserRoleId == 6 || loggedinUserRoleId == 1) {
                        this.hidebuttons = false;
                    }
                    else {
                        this.hidebuttons = true;
                    }

                    //this.latitude = -33.865143
                    //this.longitude = 151.209900;
                    this.zoom = 10;
                }
            });

            this.viewTimesheetPage = this.formBuilder.group({
                starttime: ['', Validators.required],
                endtime: ['', Validators.required],
                breakhour: ['', Validators.required],
                breakminute: ['', Validators.required],
            }, {
                    validator: PasswordValidation.StartEndDateDiffValidation // your validation method
                });

            $(document).ready(function () {
                jQuery(".md2-datepicker-arrow").hide();
                //$('#txthour').val('00');
                //$('#txtminute').val('00');

                $('.number-decimal-only').keypress(function (e) {
                    var keyCode = e.which ? e.which : e.keyCode;
                    if ((keyCode != 46 || $(this).val().indexOf('.') != -1) &&
                        (keyCode < 48 || keyCode > 57))
                        return false;
                    return true;
                });

                $('#txthour').keyup(function () {
                    if ($(this).val() > 24) {
                        alert("Hours must be less or equal to 24.");
                        $(this).val('24');
                    }
                });

                $('#txtminute').keyup(function () {
                    if ($(this).val() > 59) {
                        alert("Minutes must be less than 60.");
                        $(this).val('59');
                    }
                    if ($(this).val() == 59 && $('#txthour').val() > 23) {
                        alert("Could not allowed break more than 24 hours.");
                        $('#txthour').val('23');
                    }
                });

                $("#txtminute").blur(function () {
                    var wtime = this.worktime;
                    //alert(wtime);
                });

            });
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }

    showSwal(text: string) {
        swal({
            type: 'success',
            text: text,
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success'
        }).then(() => {
            //location.reload();
        })
    }

    showSwalAR(text: string) {
        swal({
            type: 'success',
            text: text,
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success'
        }).then(() => {
            this.router.navigate(['timesheet']);
        }).catch(swal.noop)
    }

    approvedTimeSheet(status: any) {
        //alert();
        this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));

        var contact = {
            ApprovedBy: this.userProfile.ID,
            ApprovalStatus: status
        };

        this.timesheetService.UpdateTimesheetTrack(this.commonService.baseApiUrl, this.workOrdAsgtID, contact).subscribe(
            data => {
                var datatSheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(datatSheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null) {
                    $('.loader').hide();
                }
            });
    }

    goToTimesheet() {
        this.router.navigate(['timesheet']);
    }

    getWorkOrderDetails(asgtID: any) {
        $('.loader').show();
        this.timesheetService.GetTimesheetTrack(this.commonService.baseApiUrl, asgtID).subscribe(
            data => {
                var dataTsheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(dataTsheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null) {
                    this.timesheetTrack = tSheetList;
                    this.viewTimesheetPage.controls["starttime"].setValue(tSheetList.StartTime);
                    this.viewTimesheetPage.controls["endtime"].setValue(tSheetList.EndTime);
                    if (tSheetList.TotalBreakTime != null) {
                        var spiltTime = tSheetList.TotalBreakTime.split(":");
                        this.viewTimesheetPage.controls["breakhour"].setValue(spiltTime != null ? spiltTime[0] : '00');
                        this.viewTimesheetPage.controls["breakminute"].setValue(spiltTime != null ? spiltTime[1] : '00');
                    }
                    else {
                        this.viewTimesheetPage.controls["breakhour"].setValue('00');
                        this.viewTimesheetPage.controls["breakminute"].setValue('00');
                    }
                    $('.loader').hide();
                }
                else {
                    $('.loader').hide();
                }
            }
        );
    }

    getTimesheetDaily(asgtID: any) {
        $('.loader').show();
        this.timesheetService.GetTimesheetDaily(this.commonService.baseApiUrl, asgtID).subscribe(
            data => {
                var dataTsheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(dataTsheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null && tSheetList.length > 0) {
                    var logLat = tSheetList.filter(map => map.StartLat && map.StartLong);
                    this.latitude = logLat[0].StartLat;
                    this.longitude = logLat[0].StartLong;
                    this.timesheetLatLong = tSheetList;
                    $('.loader').hide();
                }
                else {
                    $('.loader').hide();
                }
            }
        );
    }

    toggleControl(): void {
        this.showhidecontrol = !this.showhidecontrol;
    }

    saveTimesheet() {

        let starttime = this.datepipe.transform(this.viewTimesheetPage.controls["starttime"].value, 'yyyy/MM/dd HH:mm:ss');
        let endtime = this.datepipe.transform(this.viewTimesheetPage.controls["endtime"].value, 'yyyy/MM/dd HH:mm:ss');

        if (this.viewTimesheetPage.valid) {
            var contact = {
                StartTime: starttime,
                EndTime: endtime,
                WorkOrderAssignmentID: this.workOrdAsgtID,
                ID: this.tsheetDailyId
            };

            //let breaktimeinseconds: any = this.transform((this.breakhour * 3600) + (this.breakminute * 60));
            let breaktimeinseconds: any = this.viewTimesheetPage.controls["breakhour"].value + '-' + this.viewTimesheetPage.controls["breakminute"].value;

            this.timesheetService.UpdateTimesheetDaily(this.commonService.baseApiUrl, this.workOrdAsgtID + ',' + breaktimeinseconds, contact).subscribe(
                data => {
                    var datatSheet = JSON.stringify(data);
                    var tSheetParse = JSON.parse(datatSheet);
                    var tSheetList = JSON.parse(tSheetParse["_body"]);
                    if (tSheetList == true) {
                        this.showhidecontrol = false;
                        this.getWorkOrderDetails(this.tsheetDailyId);
                        this.showSwal("Timesheet has been saved successfully");
                        $('.loader').hide();
                    }
                });
        } else {
            $('.loader').hide();
            this.validateAllFormFields(this.viewTimesheetPage);
        }
    }

    approveRejectTimesheet(status: any) {

        this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));
        var contact = {
            ApprovedBy: this.userProfile.ID,
            ApprovedStatus: status,
            ID: this.tsheetDailyId
        };

        this.timesheetService.ApproveRejectTimesheet(this.commonService.baseApiUrl, this.tsheetDailyId, contact).subscribe(
            data => {
                var datatSheet = JSON.stringify(data);
                var tSheetParse = JSON.parse(datatSheet);
                var tSheetList = JSON.parse(tSheetParse["_body"]);
                if (tSheetList != null) {
                    this.getWorkOrderDetails(this.tsheetDailyId);
                    if (status == 1) {
                        this.showSwalAR("Timesheet has been approved!!");
                    }
                    else {
                        this.showSwalAR("Timesheet has been rejected!!");
                    }
                    $('.loader').hide();
                }
            });
    }

    validateAllFormFields(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.validateAllFormFields(control);
            }
        });
    }


    setTime() {
        //this.viewTimesheetPage = this.formBuilder.group({
        //    starttime: [this.datepipe.transform(this.viewTimesheetPage.controls["starttime"].value, 'yyyy/MM/dd HH:mm:ss'), Validators.required],
        //    endtime: [this.datepipe.transform(this.viewTimesheetPage.controls["endtime"].value, 'yyyy/MM/dd HH:mm:ss'), Validators.required]
        //}, {
        //        validator: PasswordValidation.StartEndDateValidation // your validation method
        //    });

        //alert(this.selectedstartdate);
        //alert(this.selectedenddate);

        if (this.selectedstartdate != "undefined" && this.selectedenddate != "undefined") {
            if (this.selectedstartdate > this.selectedenddate) {

            }
            else {
                this.worktime = (this.selectedenddate - this.selectedstartdate).toString();
                if (this.worktime != 'NaN') {

                }
            }
        }
    }

    transform(seconds) {
        let time_string: string = '';
        let plural: string = '';
        for (var key in this.times) {
            if (Math.floor(seconds / this.times[key]) > 0) {
                if (Math.floor(seconds / this.times[key]) > 1) {
                    plural = 's';
                }
                else {
                    plural = '';
                }

                time_string += Math.floor(seconds / this.times[key]).toString() + ' ' + key.toString() + plural + ' ';
                seconds = seconds - this.times[key] * Math.floor(seconds / this.times[key]);
            }
        }
        return time_string;
    }

}



