import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ViewTimesheetRoutes } from './view-timesheet.routing';
import { ViewTimesheetComponent } from './view-timesheet.component';
import { MaterialModule } from '../app.module';
import { AgmCoreModule } from '@agm/core';
import { AgmJsMarkerClustererModule } from '@agm/js-marker-clusterer';
import { Md2DatepickerModule, MdNativeDateModule } from 'md2';
import { FieldErrorDisplayModule } from '../field-error-display/field-error-display.module';
//import { TimeDurationPickerModule } from 'angular2-time-duration-picker';

@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(ViewTimesheetRoutes),
        FormsModule,
        ReactiveFormsModule,
        MaterialModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyALhBWvI6EuL2Vul0ykYis51D_ckKtxVCc',
            libraries: ["places"]
        }),
        AgmJsMarkerClustererModule,
        Md2DatepickerModule,
        MdNativeDateModule,
        FieldErrorDisplayModule
        //TimeDurationPickerModule
    ],
    declarations: [
        ViewTimesheetComponent
    ]
})

export class ViewTimesheetModule { }


